/* CONTAINERS */
.container {
    padding: 2em;
    margin-bottom: 1.5em;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.container-fluid {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.row {
    margin-right: -15px;
    margin-left: -15px;
}

.jumbotron {
    border-color: var(--primary);
    border-style: solid;
    border-width: 6px;
    border-radius: 25px;
}

.container .jumbotron,
.container-fluid .jumbotron {
    padding-right: 15px;
    padding-left: 15px;
    border-radius: 6px;
}

